.Right {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem 0.5rem;
  &-nav {
    display: flex;
    justify-content: center;
    margin-bottom: 1rem;
    &__item {
      margin: 0 1rem;
      text-transform: uppercase;
      font-size: $fs-5;
      font-weight: $fw-2;
      border: 2px solid transparent;
      cursor: pointer;
      &:hover {
        border-bottom: 2px solid $border-light;
      }
    }
  }
  &-tab {
    height: 100%;
    padding: 1rem;
    margin-bottom: 1rem;
    overflow: scroll;
  }
  &-media {
    margin-top: auto;
    & .icon {
      transition: $short-transition;
      margin: 0 0.5rem;
    }
    &__twitter {
      &:hover {
        color: $twitter;
      }
    }
    &__github {
      &:hover {
        color: $github;
      }
    }
    &__linkedin {
      &:hover {
        color: $linkedin;
      }
    }
  }
}
