* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Montserrat, -apple-system, BlinkMacSystemFont, 'Segoe UI',
    'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: $text-light;
  background: url('/images/space.jpg');
  animation: slideBackground 600s infinite;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

main {
  display: flex;
  justify-content: center;
}

section {
  width: 35%;
  height: 100vh;
}

img {
  width: 100%;
  height: auto;
}

a {
  color: inherit;
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  letter-spacing: -3px;
}

h1 {
  font-size: $fs-1;
  font-weight: $fw-1;
}

h2 {
  font-size: $fs-2;
  font-weight: $fw-2;
}

h3 {
  font-size: $fs-3;
  font-weight: $fw-2;
}

h4 {
  font-size: $fs-4;
  font-weight: $fw-2;
}

h5 {
  font-size: $fs-5;
  font-weight: $fw-2;
}

h6 {
  font-size: $fs-6;
  font-weight: $fw-2;
}

p {
  margin: 0;
  padding: 0;
  // letter-spacing: -1px;
  font-weight: $fw-3;
}

// CLASSES

.active {
  border-bottom: 2px solid $border-light !important;
}

@keyframes slideBackground {
  100% {
    background-position: 2000px -2000px;
  }
}

@keyframes slideDown {
  100% {
    opacity: 1;
    height: 100%;
    overflow: scroll;
  }
}

// MEDIA-QUERIES

@media (max-width: $desktop) {
  body {
    font-size: 12px;
    main {
      flex-direction: column;
    }
  }
}

@media (max-width: $large-screen) {
  section {
    width: 100%;
  }
}
