.Left {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 0.5rem;
  background-color: $bg-light;
  color: $text-dark;
  &-profile {
    &__image {
      display: block;
      margin: auto;
      max-width: 200px;
      border-radius: 99px;
    }
    &__name {
      margin-top: 2rem;
    }
    &__title {
      line-height: 1rem;
      font-weight: 300;
    }
  }
  &-footer {
    cursor: default;
    &__icon {
      margin: 0 0.2rem;
    }
    &:hover {
      .Left-footer__icon {
        animation: beat $long-transition infinite alternate;
      }
    }
  }
}

@keyframes beat {
  100% {
    transform: scale(1.5);
  }
}
